* {
  box-sizing: border-box;
}

.list {
  list-style: none;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-item {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 15px;
}

.country-name {
  font-size: 18px;
}

.header-info {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 15px;
}

.country-name--one {
  font-size: 30px;
}

.info-item {
  font-weight: 700;
}

.info-item-text {
  font-weight: 400;
}
